.inptsearch {
  display: flex;
  align-items: center;
  background-color: initial;
  border: none;
  padding: 0;
  text-align: inherit;
}
button,
input,
optgroup,
select,
textarea {
  line-height: inherit;
}
button {
  color: inherit;
  cursor: pointer;
}
button,
select {
  text-transform: none;
}
button,
input {
  overflow: visible;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.inptsearchsvg {
  height: 20px;
  width: 20px;
  padding-left: 14px;
  fill: #767676;
  box-sizing: initial;
  transition: fill 0.1s ease-in-out;
}
.inptsearchtext {
  display: flex;
  flex-grow: 1;
}

.inptsearchtextinput {
  -webkit-appearance: none;
  box-shadow: none;
  padding-bottom: 2px;
  padding-left: 10px;
  background: none;
  border: none;
  color: #111;
  flex-grow: 1;
  width: 100%;
}
.divider {
  background-color: #d1d1d1;
  content: "";
  height: 32px;
  width: 1px;
}

.loginsignup {
  align-items: center;
  color: #767676;
  display: flex;
}

.loginsignup > a {
  font-size: 14px;
  color: #767676;
  height: 32px;
  line-height: 30px;
  padding: 0 11px;
  fill: currentColor;
  border: 1px solid #0000;
  border-radius: 4px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  transition: all 0.1s ease-in-out;
  -webkit-user-select: none;
  user-select: none;
  white-space: nowrap;
  font-weight: 500;
  background-color: initial;
}
.rMj7l {
