.grandparentcontainer {
  background: #fff;
  position: sticky;
  z-index: 2;
}
@media (min-width: 768px) {
  .grandparentcontainer {
    top: 62px;
  }
}

.parentcontainer {
  box-shadow: 0 4px 12px #00000014, 0 0 1px #0100001a;
}
@media (min-width: 768px) {
  .parentcontainer {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.container {
  --subNavGutter: 24px;
  align-items: center;
  display: flex;
  margin-bottom: 0;
  margin-left: calc(var(--subNavGutter) * -1);
}

.leftcontainer,
.rightcontainer {
  --subNavGutter: 24px;
  padding-bottom: 0;
  padding-left: var(--subNavGutter);
}

.leftcontainerul {
  --subNavGutter: 24px;
  align-items: center;
  display: flex;
  margin-bottom: 0;
  margin-left: calc(var(--subNavGutter) * -1);
  list-style: none;
  padding-left: 0;
  margin-top: 0;
}

.leftcontainerulli {
  --subNavGutter: 24px;
  padding-bottom: 0;
  padding-left: var(--subNavGutter);
}

.leftcontainerullia1 {
  /* box-shadow: inset 0 -2px #111; */
  color: #111;
  pointer-events: none;
  align-items: center;
  display: flex;
  font-size: 14px;
  height: 56px;
  text-decoration: none;
  white-space: nowrap;
  text-decoration-skip-ink: auto;
  transition: color 0.1s ease-in-out, opacity 0.1s ease-in-out;
  background-color: initial;
}

.leftcontainerullia {
  align-items: center;
  display: flex;
  font-size: 14px;
  height: 56px;
  text-decoration: none;
  white-space: nowrap;
}

.rightcontaierspace {
  --subNavGutter: 24px;
  background-color: #d1d1d1;
  height: 32px;
  width: 1px;
}

.rightmostcontainerparent {
  flex-grow: 1;
  min-width: 0;
  --subNavGutter: 24px;
  padding-bottom: 0;
  padding-left: var(--subNavGutter);
}

.rightmostcontainer {
  isolation: isolate;
  position: relative;
}

.rightcontaiericonbtn {
  padding-left: 8px;
  align-items: center;
  display: flex;
  height: 40px;
  justify-content: center;
  background-color: initial;
  border: none;
  padding: 0;
  text-align: inherit;
}

[type="button"],
[type="reset"],
[type="submit"],
button {
  -webkit-appearance: button;
}
button,
input,
optgroup,
select,
textarea {
  line-height: inherit;
}
button {
  color: inherit;
  cursor: pointer;
}
button,
input {
  overflow: visible;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

.rightcontaiericonbtnsvg {
  height: 24px;
  width: 24px;
  margin-right: -6px;
  fill: #767676;
  height: 22px;
  transition: fill 0.1s ease-in-out;
  width: 22px;
}
.rightmostcontainercontent {
  box-sizing: initial;
  display: flex;
  /* overflow-y: hidden; */
  scroll-behavior: smooth;
  margin-top: -39px;
}

.containercontentul {
  --subNavGutter: 24px;
  align-items: center;
  display: flex;
  margin-bottom: 0;
  margin-left: calc(var(--subNavGutter) * -1);
  list-style: none;
  padding-left: 0;
  margin-top: 0;
}

.containercontentulli {
  --subNavGutter: 24px;
  padding-bottom: 0;
  padding-left: var(--subNavGutter);
}

.containercontentullia {
  color: #767676;
  pointer-events: none;
  align-items: center;
  display: flex;
  font-size: 14px;
  height: 56px;
  text-decoration: none;
  white-space: nowrap;
  text-decoration-skip-ink: auto;
  transition: color 0.1s ease-in-out, opacity 0.1s ease-in-out;
}

.containercontentulliafter {
  color: #767676;
  pointer-events: none;
  align-items: center;
  display: flex;
  font-size: 14px;
  height: 56px;
  text-decoration: none;
