a {
  background-color: initial;
}

.rbtdiv {
  right: 0;
  align-items: center;
  display: flex;
  height: 100%;
  position: absolute;
  z-index: 2;
}

.rbtn {
  padding-left: 8px;
  align-items: center;
  display: flex;
  height: 40px;
  justify-content: center;
  border: none;
  background: transparent;
}

